$primary: #540cfa;
$white: #ffffff;
$black: #000000;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul,
ol {
  list-style: none;
}

img {
  max-width: 100%;
}

a {
  color: $black;
  text-decoration: none;
}

body {
  color: $black;
  overflow-x: hidden;
  font: normal 16px/1 "Source Sans Pro";
}

input,
button,
textarea,
select {
  outline: none;
  font: normal 16px/1 "Source Sans Pro";
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1130px;
}

.row {
  display: flex;
  flex-wrap: wrap;
}

.col-md-6 {
  width: 50%;
  flex: 0 0 50%;
  max-width: 50%;
}

.col-md-4 {
  width: 33.33%;
  flex: 0 0 33.33%;
  max-width: 33.33%;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 99;
  padding: 9px 0;
  background-color: $white;
  box-shadow: 0 0 5px -2px rgba($black, 0.6);
  .row {
    align-items: center;
    justify-content: space-between;
  }
}

.logo {
  .logo-inner {
    font-size: 18px;
    font-weight: bold;
    span {
      color: $primary;
    }
  }
}

.nav ul {
  display: flex;
  align-items: center;
  li {
    margin-right: 110px;
    &:last-child {
      margin-right: 0;
    }
    a {
      font-size: 14px;
    }
  }
}

.nav-right {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  a {
    font-size: 14px;
    font-weight: 600;
  }
  &__cta {
    color: $white;
    padding: 9px 17px;
    margin-left: 30px;
    border-radius: 4px;
    background-color: $primary;
    box-shadow: 0px 2px 4px rgba(60, 9, 179, 0.3);
  }
}

main {
  padding-top: 50px;
}

.hero {
  padding-top: 85px;
  padding-bottom: 158px;
  background: url("../images/hero-bg.png") no-repeat top right,
    url("../images/hero-bg-2.png") no-repeat bottom -100px left;
}

.hero-title {
  margin-top: 65px;
  &__h1 {
    font-size: 70px;
    line-height: 72px;
    font-weight: 900;
  }
  &__desc {
    width: 471px;
    max-width: 100%;
    font-size: 21px;
    font-weight: 300;
    line-height: 32px;
    margin: 19px 0 29px 0;
  }
}

.hero-search {
  width: 450px;
  max-width: 100%;
  position: relative;
  &__label {
    position: absolute;
    top: 10px;
    left: 15px;
    color: $primary;
    font-size: 12px;
    font-weight: bold;
  }
  &__input {
    width: 100%;
    height: 53px;
    color: $black;
    font-size: 16px;
    padding-top: 15px;
    padding-left: 15px;
    border-radius: 4px;
    padding: 15px 150px 0 15px;
    border: 1px solid rgba($black, 0.15);
    &::placeholder {
      opacity: 0.3;
    }
  }
  &__cta {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    cursor: pointer;
    border: none;
    color: $white;
    font-size: 16px;
    min-width: 140px;
    font-weight: bold;
    text-align: center;
    background-color: $primary;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
  }
}

.hero-compatible {
  margin-top: 125px;
  &__label {
    opacity: 0.3;
    color: $black;
    display: block;
    font-size: 12px;
    font-weight: 600;
    line-height: 15px;
    letter-spacing: 2px;
    margin-bottom: 15px;
    mix-blend-mode: normal;
    text-transform: uppercase;
  }
}

.hero-image {
  padding-left: 122px;
}

.section-title {
  width: 100%;
  margin: 0 auto;
  max-width: 610px;
  text-align: center;
  &__span {
    opacity: 0.8;
    font-size: 18px;
    font-weight: 300;
  }
  &__h2 {
    font-size: 60px;
    font-weight: 900;
    line-height: 63px;
    margin: 15px 0 20px 0;
  }
  &__p {
    opacity: 0.8;
    font-size: 21px;
    line-height: 32px;
  }
}

.feature-items {
  margin: 80px 0 140px 0;
  .feature-item {
    padding: 0 15px;
    text-align: center;
    &__h3 {
      margin-top: 8px;
      font-size: 18px;
      font-weight: bold;
    }
    &__p {
      opacity: 0.7;
      width: 100%;
      font-size: 16px;
      max-width: 285px;
      line-height: 24px;
      margin: 8px auto 10px auto;
    }
    &__link {
      display: inline-block;
      position: relative;
      color: $primary;
      font-size: 16px;
      font-weight: 600;
      padding-right: 14px;
      &:before {
        position: absolute;
        content: "";
        width: 5px;
        height: 5px;
        top: 6px;
        right: 0;
        transform: rotate(135deg);
        border-top: 2px solid $primary;
        border-left: 2px solid $primary;
      }
    }
  }
}

.featured-shows {
  margin-bottom: 138px;
}
.shows-wrapper {
  .item-show {
    padding: 0 15px;
    img {
      width: 220px;
      margin: 0 auto;
      border-radius: 6px;
      box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
    }
  }
}

.ui-features {
  overflow-x: hidden;
  margin-bottom: 80px;
  .section-title {
    margin: 80px 0 0;
    text-align: left;
    max-width: 460px;
    &__h2 {
      font-size: 46px;
      line-height: 50px;
    }
    &__p {
      font-size: 18px;
      line-height: 28px;
    }
  }

  &__tab {
    ul {
      margin-top: 28px;
      li {
        margin-top: 15px;
        a {
          opacity: 0.5;
          display: flex;
          padding: 18px;
          font-size: 16px;
          font-weight: 600;
          align-items: center;
          img {
            margin-right: 18px;
          }
        }
        &.active {
          a {
            opacity: 1;
          }
        }
      }
    }
  }
  .ui-image {
    img {
      max-width: none;
    }
    @media screen and (min-width: 1600px) {
      padding-left: 50px;
    }
  }
}

.community-section {
  color: $white;
  overflow: hidden;
  max-height: 800px;
  background-color: $black;
  .row {
    > .col-md-6 {
      &.left {
        align-self: center;
      }
    }
  }
  .section-title {
    margin: 0;
    text-align: left;
    max-width: 460px;
    &__h2 {
      font-size: 46px;
      line-height: 50px;
    }
    &__p {
      opacity: 0.8;
      font-size: 18px;
      line-height: 28px;
    }
    &__cta {
      color: $white;
      padding: 11px;
      font-size: 16px;
      margin-top: 25px;
      min-width: 216px;
      font-weight: 600;
      line-height: 20px;
      border-radius: 4px;
      text-align: center;
      background: $primary;
      display: inline-block;
      box-shadow: 0px 1px 2px rgba(32, 5, 97, 0.08),
        0px 2px 4px rgba(32, 5, 97, 0.1);
    }
  }
}

.tweet-section {
  .row {
    margin: 0 -10px;
  }
  .tweet-inner {
    padding: 0 10px;
    &.left {
      margin-top: -85px;
    }
    &.right {
      margin-top: -200px;
    }
  }
}

.tweet-item {
  border-radius: 10px;
  margin-bottom: 20px;
  padding: 16px 16px 20px 16px;
  background-color: rgba($white, 0.08);
  background-image: url("../images/user/tweet-icon.svg");
  background-repeat: no-repeat;
  background-position: top 25px right 16px;
  &:last-child {
    margin-bottom: 0;
  }
  &__user {
    display: flex;
    align-items: center;
    img {
      margin-right: 10px;
    }
    span {
      font-size: 12px;
      font-weight: 600;
    }
  }
  &__desc {
    opacity: 0.8;
    font-size: 13px;
    line-height: 20px;
    margin: 15px 0 35px 0;
  }
  &__date {
    opacity: 0.5;
    font-size: 12px;
  }
}

.pre-footer {
  margin: 135px 0 90px 0;
  .hero-search {
    margin: 58px auto 0 auto;
  }
}

.footer {
  padding: 25px 0;
  text-align: center;
  &__copyright {
    opacity: 0.4;
    margin-top: 25px;
    font-size: 12px;
    font-weight: 600;
    line-height: 18px;
    a {
      text-decoration: underline;
    }
  }
}

.social-icons {
  display: flex;
  align-items: center;
  justify-content: center;
  li {
    margin: 0 12px;
    a {
      opacity: 0.2;
    }
  }
}

@media screen and (max-width: 1130px) {
  .container {
    max-width: 950px;
  }
}

@media screen and (max-width: 991px) {
  .nav {
    ul {
      li {
        margin-right: 50px;
      }
    }
  }
  .hero {
    padding: 85px 0;
  }
  .hero-title {
    &__h1 {
      font-size: 42px;
      line-height: 48px;
    }
    &__desc {
      width: 390px;
      font-size: 18px;
      line-height: 27px;
    }
  }
  .hero-image {
    padding-left: 62px;
  }
  .hero-compatible {
    margin-top: 95px;
  }

  .section-title {
    &__span {
      font-size: 16px;
    }
    &__h2 {
      font-size: 35px;
      line-height: 40px;
    }
    &__p {
      font-size: 18px;
      line-height: 27px;
    }
  }

  .feature-items {
    margin: 60px 0 80px 0;
  }

  .featured-shows {
    margin-bottom: 65px;
  }

  .ui-features {
    margin-bottom: 30px;
    .section-title {
      &__h2 {
        font-size: 32px;
        line-height: 38px;
      }
      &__p {
        font-size: 16px;
        line-height: 24px;
      }
    }
    &__tab {
      ul {
        li {
          a {
            padding: 14px;
          }
        }
      }
    }
  }
  .community-section {
    max-height: none;
    padding-top: 70px;
    .row > {
      .col-md-6 {
        width: 100%;
        flex: 0 0 100%;
        max-width: 100%;
      }
    }
    .section-title {
      &__h2 {
        font-size: 32px;
        line-height: 38px;
      }
      &__p {
        font-size: 16px;
        line-height: 24px;
      }
    }
  }
  .tweet-section {
    margin: 50px 0 90px 0;
    .row {
      flex-direction: row;
      overflow: unset;
      overflow-x: auto;
      -webkit-overflow-scrolling: touch;
    }
    .tweet-inner {
      &.left,
      &.right {
        margin: 0;
        display: none;
      }
      &.left {
        display: flex;
        flex-wrap: nowrap;
      }
    }
  }
  .tweet-item {
    width: 260px;
    flex: 0 0 260px;
    max-width: 260px;
    margin-bottom: 0;
    margin-right: 20px;
  }
  .pre-footer {
    margin: 90px 0;
  }
}

@media screen and (max-width: 767px) {
  .col-sm-12 {
    width: 100%;
    flex: 0 0 100%;
    max-width: 100%;
  }
  .nav {
    display: none;
  }
  .nav-right {
    &__cta {
      padding: 9px 10px;
      margin-left: 15px;
    }
  }
  .hero {
    padding: 35px 0;
    background: url("../images/hero-bg.png") no-repeat top 90% right -360px,
      url("../images/hero-bg-2.png") no-repeat bottom -40px left;
  }
  .hero-title {
    margin-top: 0;
  }
  .hero-compatible {
    margin: 40px 0 30px 0;
  }
  .hero-image {
    padding: 0;
    text-align: center;
  }
  .section-title {
    text-align: left;
    max-width: 100%;
  }
  .feature-items {
    margin: 0 0 50px 0;
    .feature-item {
      padding: 0;
      text-align: left;
      margin-top: 40px;
      &__p {
        margin-left: 0;
      }
    }
  }
  .featured-shows {
    margin-bottom: 0;
  }
  .ui-features {
    .section-title {
      max-width: 100%;
      margin: 60px 0 0;
    }
    .ui-image {
      margin-top: 40px;
    }
  }
  .pre-footer {
    margin: 60px 0;
    .section-title {
      text-align: center;
    }
    .hero-search {
      margin: 38px auto 0 auto;
    }
  }
}
